:root {
  --title-primary: #000;
  --subtitle-primary: #2a3246;
  --subtitle-second: #263238;
  --text-primary: #8f95a5;
  --text-second: #546269;
  --white: #fff;
  --button-background: #263238;
  --button-accent-background: #1a3c4c;
  --mobile-menu-color: #39515e;
  --card-background: #f5f5f5;
  --price-background: #eee;
  --logo-primary: #000;
  --logo-second: #939393;
  --transition: 250ms linear;
  --indent: 40px;
  --items: 3;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.card-set {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.card-set__item {
  flex-basis: calc((100% - var(--indent) * (var(--items)  - 1) ) / var(--items) );
}

.icon {
  fill: currentColor;
}

.button {
  white-space: nowrap;
  border: 1px solid var(--button-background);
  background-color: var(--white);
  color: var(--title-primary);
  cursor: pointer;
  transition: background-color var(--transition), box-shadow var(--transition), border-color var(--transition);
  border-radius: 10px;
  padding: 8px 56px;
}

@media screen and (max-width: 767px) {
  .button {
    letter-spacing: .03em;
    font-size: 16px;
    font-weight: 400;
    line-height: 1.5;
  }
}

@media screen and (min-width: 768px) {
