.menu-button {
  width: 22px;
  height: 22px;
  z-index: 110;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: relative;
  overflow: hidden;
}

.menu-button.is-open {
  position: fixed;
  right: 20px;
}

@media screen and (min-width: 1200px) {
  .menu-button {
    display: none;
  }
}

.burger-menu {
  width: 100%;
  height: 2px;
  background-color: var(--mobile-menu-color);
  transition: transform var(--transition), background-color var(--transition);
  border-radius: 4px;
  position: absolute;
  left: 0;
}

.burger-menu:before, .burger-menu:after {
  content: "";
  width: 100%;
  height: 2px;
  background-color: var(--mobile-menu-color);
  z-index: 120;
  transition: transform var(--transition), background-color var(--transition);
  border-radius: 4px;
  position: absolute;
  left: 0;
}

.burger-menu:before {
  width: 75%;
  top: 8px;
}

.burger-menu:after {
  top: -8px;
}

.is-open .burger-menu {
  background-color: rgba(0, 0, 0, 0);
}

.is-open .burger-menu:before, .is-open .burger-menu:after {
  background-color: var(--mobile-menu-color);
  top: 0;
}

.is-open .burger-menu:before {
  width: 100%;
  transform: rotate(45deg);
}

.is-open .burger-menu:after {
  transform: rotate(-45deg);
}

.backdrop {
  min-width: 320px;
  width: 100vw;
  height: 100vh;
  z-index: 420;
  visibility: visible;
  opacity: 1;
  transition: opacity var(--transition), visibility var(--transition);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

